.target-repack{

    .target-repack-mobile{
        .rt-thead{
            width: 700px !important;
        }
        .rt-tbody{
            overflow: hidden !important;
            max-height: 100px !important;
            width: 700px !important;
        }
        .rt-th{
            display: flex;
            justify-content: flex-end;
        }
        .rt-th:nth-child(2){
            display: flex;
            justify-content: flex-start;
        }
    }

    .target-repack{
        .rt-tbody{
            overflow: hidden !important;
            max-height: 145px;
        }
        .rt-th{
            display: flex;
            justify-content: flex-end;
        }
        .rt-th:nth-child(2){
            display: flex;
            justify-content: flex-start;
        }
    }

    .target-movement-mobile{
        .rt-thead{
            width: 700px !important;
        }

        .rt-tbody{
            width: 700px !important;
        }

        .rt-tbody{
            overflow: hidden !important;
            max-height: 100px !important;
            width: 100%;
        }
        .rt-th{
            display: flex;
            justify-content: flex-end;
        }
        .rt-th:nth-child(1){
            display: flex;
            justify-content: flex-end;
            min-width: 300px !important;
        }
        .rt-td:nth-child(1){
            display: flex;
            justify-content: flex-end;
            min-width: 300px !important;
        }

        .rt-th:nth-child(2){
            display: flex;
            justify-content: flex-end;
            min-width: 150px !important;
        }
        .rt-td:nth-child(2){
            display: flex;
            justify-content: flex-end;
            min-width: 150px !important;
        }

        .rt-th:nth-child(3){
            min-width: 150px !important;
        }
        .rt-td:nth-child(3){
            min-width: 150px !important;
        }

        .rt-th:nth-child(4){
            min-width: 100px !important;
        }
        .rt-td:nth-child(4){
            min-width: 100px !important;
        }
    }

    .target-movement{
        .rt-tbody{
            overflow: hidden !important;
            max-height: 145px;
        }
        .rt-th{
            display: flex;
            justify-content: flex-end;
        }
        .rt-th:nth-child(2){
            display: flex;
            justify-content: flex-end;
        }
    }
    
}

.ReactTable{
    background-color: white !important;
    border-radius: 4px !important;
    .rt-table{
        // overflow: hidden !important;
        border-radius: 4px !important;
    }
    .rt-tbody{
        // overflow: auto !important;
        background-color: white !important;
    }
    .rt-thead{
        box-shadow: none !important;
        border-bottom: 1px solid var(--lineGrey);
        height: 44px !important;
    }
    .table{ 
        position: relative;
    } 
      
    .rt-th{
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        color: var(--emptyTextField);
        padding: 10px !important;
        position: sticky;
        display: flex;
        align-items: center;
    }
    .rt-th:focus{
        outline: none !important;
    }
    .rt-td{
        font-size: 14px;
        color: var(--text);
        padding: 10px !important;
        height: 2rem;
    }
 
    
    .-sort-desc,.-sort-asc {
        box-shadow: none !important; 
    } 

    .iconU-sort{
        font-size: 8pt;
        margin-left:0.5rem;
    }

    .-cursor-pointer .iconU-sort:before {
        content: "\e928";
    } 
    .-cursor-pointer.-sort-asc .iconU-sort:before {
        content: "\e929";
        color:#4abe84;
    } 
    .-cursor-pointer.-sort-desc .iconU-sort:before {
        content: "\e927";
        color:#4abe84;
    } 

    .-cursor-pointer.-sort-asc {
        color:#4abe84;
    } 
    .-cursor-pointer.-sort-desc{
        color:#4abe84;
    }
    .rt-resizable-header {
        padding: 6px 24px !important;
        -webkit-box-shadow: none !important;
        box-shadow: none !important;
    }
     
    .rt-resizer:before {
        display: inline-block;
        position: absolute;
        right: 25px;
        top: 3px;
        height: 18px;
        width: 18px;
        color: transparent;
        content: '.';
        background-size: 18px 18px;
        background-repeat: no-repeat; 
        opacity: 0.87;
    }
    
    .rt-resizable-header-content:after {
        position: absolute;
        right: 8px;
        top: 3px;
        height: 18px;
        width: 18px;
        z-index: 120;
        color: transparent;
        content: '.';
    }
    
     .rt-th.-sort-asc .rt-resizable-header-content:after {
         content: "▲";
         color: black;
    }
    
     .rt-th.-sort-desc .rt-resizable-header-content:after {
          content: "▼";
          color: black;
    }
}

.target-repack{
    .rt-noData{
        margin-top: 7rem
    }
